<template>
  <div id="meeting">
    <div class="weui-cells__title"></div>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" placeholder="" v-model="name">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="number" pattern="[0-9]*" placeholder="" v-model="phone">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" placeholder="" v-model="activity">
        </div>
      </div>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <a class="weui-cell weui-cell_access" @click="datePick">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <p>{{ date }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access" @click="timePick">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <p>{{ time }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells weui-cells_checkbox">
      <label class="weui-cell weui-check__label" for="projection">
        <div class="weui-cell__hd">
          <input type="checkbox" class="weui-check" name="checkbox" id="projection" v-model="isPNeed">
          <i class="weui-icon-checked"></i>
        </div>
        <div class="weui-cell__bd">
          <p></p>
        </div>
      </label>
    </div>
    <div class="weui-cells__tips"></div>
    <p class="weui-btn-area">
      <a @click="meetingBookCreate" class="weui-btn weui-btn_primary"></a>
    </p>
  </div>
</template>

<script>
import api from '../api'
import logmsg from './logmsg'
import weui from 'weui.js'
export default {
  components: {
    logmsg
  },
  data () {
    return {
      userId: JSON.parse(window.atob(this.$store.state.token.split('.')[1])).id,
      name: '',
      phone: '',
      activity: '',
      date: '',
      time: '',
      isPNeed: false,
      occupiedTime: {}
    }
  },
  methods: {
    meetingBookCreate () {
      if (this.name && this.phone && this.activity && this.date !== '' && this.time !== '' && this.time !== '') {
        let loading = weui.loading('')
        api.meetingBookCreate({
          meetingBook: {
            userId: this.userId,
            name: this.name,
            phone: this.phone,
            activity: this.activity,
            date: this.date,
            time: this.time,
            isPNeed: this.isPNeed,
            condition: 'book'
          }
        }).then((res) => {
          loading.hide()
          if (res.status === 200) {
            weui.toast('', 1500)
            this.name = ''
            this.phone = ''
            this.activity = ''
            this.date = ''
            this.time = ''
            this.isPNeed = false
          } else {
            weui.alert(res.data.msg)
          }
        }).catch((err) => {
          loading.hide()
          console.error(err)
          weui.alert('')
        })
      } else {
        weui.alert('')
      }
    },
    datePick () {
      const now = new Date()
      const day = ['', '', '', '', '', '', '']
      let end = new Date()
      let dateList = []
      for (let i = 0; i !== 5; ++i) {
        end.setTime(now.getTime() + 86400000 * i)
        dateList.push({
          label: `${end.getFullYear()}${end.getMonth() + 1}${end.getDate()}${day[end.getDay()]}`,
          value: i
        })
      }
      weui.picker(dateList, {
        onConfirm: (result) => {
          this.date = result[0].label
          this.time = ''
        },
        id: 'date-picker'
      })
    },
    timePick () {
      if (this.time !== '') {
        let loading = weui.loading('')
        api.meetingOccupiedTimeGet()
          .then((res) => {
            loading.hide()
            if (res.status === 200) {
              this.occupiedTime = res.data.occupiedTime
              let options = []
              if (!this.occupiedTime[`${this.date} 12:30-14:00`]) {
                options.push({
                  label: ' 12:30-14:00',
                  value: 0
                })
              }
              if (!this.occupiedTime[`${this.date} 17:30-19:00`]) {
                options.push({
                  label: ' 17:30-19:00',
                  value: 1
                })
              }
              if (!this.occupiedTime[`${this.date} 19:00-20:30`]) {
                options.push({
                  label: ' 19:00-20:30',
                  value: 2
                })
              }
              if (!this.occupiedTime[`${this.date} 20:30-22:00`]) {
                options.push({
                  label: ' 20:30-22:00',
                  value: 3
                })
              }
              if (options.length === 0) {
                options.push({
                  label: '',
                  value: -1
                })
              }
              weui.picker(options, {
                onConfirm: (result) => {
                  if (result[0].value !== -1) {
                    this.time = result[0].label
                  }
                },
                id: 'time-picker'
              })
            } else {
              weui.alert(res.data.msg)
            }
          }).catch((err) => {
            loading.hide()
            console.error(err)
            weui.alert('')
          })
      }
    }
  }
}
</script>



