<template>
  <div id="material">
    <div class="weui-cells__title"></div>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" placeholder="" v-model="name">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="number" pattern="[0-9]*" placeholder="" v-model="phone">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" placeholder="" v-model="activity">
        </div>
      </div>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <a class="weui-cell weui-cell_access" @click="takeDatePick">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <p>{{ takeDate }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access" @click="returnDatePick">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <p>{{ returnDate }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <a class="weui-cell weui-cell_access" @click="numberPick(index, materialBookItem, materials[materialBookItem.index].left)" v-for="(materialBookItem, index) in materialBookItems">
        <div class="weui-cell__hd"><label class="weui-label">{{ materials[materialBookItem.index].name }}</label></div>
        <div class="weui-cell__bd">
          <p v-show="!materialBookItem.book">{{ materials[materialBookItem.index].left > 30 ? 30 : materials[materialBookItem.index].left }}{{ materials[materialBookItem.index].unit }}</p>
          <p v-show="materialBookItem.book">{{ materialBookItem.book}}{{ materials[materialBookItem.index].unit }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access" @click="materialPick">
        <div class="weui-cell__bd">
          <p></p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
    </div>
    <div class="weui-cells__tips">17:30-18:00</div>
    <p class="weui-btn-area">
      <a @click="materialBookCreate" class="weui-btn weui-btn_primary"></a>
    </p>
  </div>
</template>

<script>
import api from '../api'
import logmsg from './logmsg'
import weui from 'weui.js'
export default {
  components: {
    logmsg
  },
  data () {
    return {
      userId: JSON.parse(window.atob(this.$store.state.token.split('.')[1])).id,
      name: '',
      phone: '',
      activity: '',
      takeDate: '',
      returnDate: '',
      materialBookItems: [],
      materials: []
    }
  },
  methods: {
    materialBookCreate () {
      if (this.name && this.phone && this.activity && this.takeDate !== '' && this.returnDate !== '' && this.returnDate !== '' && this.materialBookItems.length) {
        let loading = weui.loading('')
        api.materialBookCreate({
          materialBook: {
            userId: this.userId,
            name: this.name,
            phone: this.phone,
            activity: this.activity,
            takeDate: this.takeDate,
            returnDate: this.returnDate,
            remark: '',
            condition: 'book'
          },
          materialBookItems: this.materialBookItems
        }).then((res) => {
          loading.hide()
          if (res.status === 200) {
            weui.toast('', 1500)
            this.name = ''
            this.phone = ''
            this.activity = ''
            this.takeDate = ''
            this.returnDate = ''
            this.materialBookItems = []
            this.materials = []
          } else {
            weui.alert(res.data.msg)
          }
        }).catch((err) => {
          loading.hide()
          console.error(err)
          weui.alert('')
        })
      } else {
        weui.alert('')
      }
    },
    takeDatePick () {
      const now = new Date()
      const day = ['', '', '', '', '', '', '']
      let end = new Date()
      let dateList = []
      for (let i = 0; i !== 5; ++i) {
        end.setTime(now.getTime() + 86400000 * i)
        if (end.getDay() === 1 || end.getDay() === 3 || end.getDay() === 5) {
          dateList.push({
            label: `${end.getFullYear()}${end.getMonth() + 1}${end.getDate()}${day[end.getDay()]}`,
            value: i
          })
        }
      }
      weui.picker(dateList, {
        onConfirm: (result) => {
          this.takeDate = result[0].label
          this.returnDate = ''
        },
        id: 'take-date-picker'
      })
    },
    returnDatePick () {
      if (this.returnDate !== '') {
        const day = ['', '', '', '', '', '', '']
        const yearIndex = this.takeDate.indexOf('')
        const monthIndex = this.takeDate.indexOf('')
        const dateIndex = this.takeDate.indexOf('')
        let startDate = new Date()
        startDate.setFullYear(parseInt(this.takeDate.slice(0, yearIndex)), parseInt(this.takeDate.slice(yearIndex + 1, monthIndex) - 1), parseInt(this.takeDate.slice(monthIndex + 1, dateIndex)))
        let end = new Date()
        let dateList = []
        for (let i = 0; i !== 5; ++i) {
          end.setTime(startDate.getTime() + 86400000 * i)
          if (end.getDay() === 1 || end.getDay() === 3 || end.getDay() === 5) {
            dateList.push({
              label: `${end.getFullYear()}${end.getMonth() + 1}${end.getDate()}${day[end.getDay()]}`,
              value: i
            })
          }
        }
        weui.picker(dateList, {
          onConfirm: (result) => {
            this.returnDate = result[0].label
          },
          id: 'return-date-picker'
        })
      }
    },
    numberPick (index, materialBookItem, n) {
      let options = []
      options.push({
        label: ``,
        value: 0
      })
      if (n === -1) {
        options.push({
          label: '',
          value: -1
        })
      } else {
        n = n > 30 ? 30 : n
        for (let i = 1; i <= n; ++i) {
          options.push({
            label: `${i}`,
            value: i
          })
        }
      }
      weui.picker(options, {
        defaultValue: [1],
        onConfirm: (result) => {
          if (result[0].value) {
            materialBookItem.book = result[0].value
          } else {
            this.materialBookItems.splice(index, 1)
          }
        },
        id: 'number-picker'
      })
    },
    materialPick () {
      let loading = weui.loading('')
      api.materialListGet()
        .then((res) => {
          loading.hide()
          if (res.status === 200) {
            this.materials = res.data.materialList
            let options = []
            if (this.materials.length === this.materialBookItems.length) {
              options.push({
                label: '',
                value: -1
              })
            } else {
              for (let i = 0; i < this.materials.length; ++i) {
                let isSelected = false
                for (let selectedItem of this.materialBookItems) {
                  if (selectedItem.index === i) {
                    isSelected = true
                    break
                  }
                }
                if (!isSelected) {
                  options.push({
                    label: `${this.materials[i].name}`,
                    value: i
                  })
                }
              }
            }
            weui.picker(options, {
              onConfirm: (result) => {
                if (result[0].value !== -1) {
                  this.materialBookItems.push({
                    index: result[0].value,
                    userId: this.userId,
                    materialId: this.materials[result[0].value]._id,
                    book: 0,
                    condition: 'book'
                  })
                }
              },
              id: 'material-picker'
            })
          } else {
            weui.alert(res.data.msg)
          }
        })
        .catch((err) => {
          loading.hide()
          console.error(err)
          weui.alert('')
        })
    }
  }
}
</script>



